.paginator {
    display: block;
    margin: 10px auto;
    width: 550px;
}

.pageNumber {
    display: inline-block;
    text-align: center;
    width: 20px;
    height: 20px;
    padding: 5px;
    margin: 0 2px;
    border: 2px solid grey;
    background-color: white;
}

.selectedPage {
    font-weight: bold;
    border-color: black;
}

.directButton {
    width: 50px;
    height: 30px;
    background-color: lightblue;
    border: 2px solid grey;
    margin: 0 5px;
}